import React from "react";
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import "./App.css";
import Home from "./Home";
import Navbar from "./components/Navbar";
import Contact from "./components/Contact";
import ExecsPage from "./pages/Execs";
import SurvivalGuide from "./components/SurvivalGuide";
import NotFound from "./components/NotFound"; // Import NotFound component
import AdART from "./components/AdART";
import NewsPage from "./components/News";
import SingleNews from "./components/SingleNews";

function App() {
  return (
    <Router>
      <Navbar />
      <Routes>
        <Route path="/" element={<Home />} />
        <Route path="/faq" element={<Contact />} />
        <Route path="/survival-guide" element={<SurvivalGuide />} />
        <Route path="/team" element={<ExecsPage />} />
        <Route path="/adart" element={<AdART />} />
        <Route path="/news" element={<NewsPage />} />
        <Route path="/news/:slug" element={<SingleNews />} />

        {/* Catch-all route for invalid paths */}
        <Route path="*" element={<NotFound />} />
      </Routes>
    </Router>
  );
}

export default App;
